.noDrag {
  height: calc(100% - 20px);
  transform: none !important;
}

.react-grid-item.react-grid-placeholder {
  background-color: #30367c !important;
  opacity: 0.2;
  transition-duration: 100ms;
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

